.productCard{
   width: 300px;
   display: flex;
   flex-direction: column;
   gap: 10px;
   
   .image{
    width: 100%;
    height: 400px;
    overflow: hidden;
    position: relative;

    &:hover{
        .secondImage{
            z-index: 2;
        }
    }

    img{
        width: 100%;
        height: 100%;
        object-fit: cover;
        position: absolute;
    }

    .mainImage{
        z-index: 1;
    }

    span{
        position: absolute;
        top: 5px;
        left: 5px;
        background-color: white;
        color: teal;
        padding: 3px 5px;
        z-index: 3;
        font-weight: 500;
        font-size: 12px;
        border-radius: 5px 5px 5px 5px;
    }
   }

   h2{
    font-size: 16px;
    font-weight: 400;
   }

   .prices{
    display: flex;
    gap: 20px;

    h3{
        font-size: 18px;
        font-weight: 500;

        &:first-child{
            color: grey;
            text-decoration: line-through;
        }
    }
   }
}